body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  text-align: center;
  background-image: linear-gradient(
    to right,
    #e4afcb 0%,
    #b8cbb8 0%,
    #b8cbb8 0%,
    #e2c58b 30%,
    #c2ce9c 64%,
    #7edbdc 100%
  );
}

h1 {
  margin: 0;
  margin-bottom: 0.5rem;
  font-size: 3.5rem;
  font-family: "Roboto", sans-serif;
  align-items: center;
  color: rgb(110, 63, 63);
}

.input {
  font-size: 2.5rem;
  border: 1px solid black;
  border-radius: 0.3rem;
  padding: 0.25rem;
  width: 7em;
  background: rgb(165, 165, 165);
}

select {
  font-size: 2.5rem;
  margin-left: 0.5rem;
  background: rgb(253, 209, 192);
}

.equals {
  font-weight: bold;
  font-size: 3rem;
}
